:root {
  --primary-text-color: #2c2c2c;
  --primary-background-color: #fefefe;
  --white-color: #fff;
  --dark-green-color: #7eb101;
  --btn-1_clr__default: #bbf330;
  --btn-1_clr__hover: #b5ff00;
  --hdr_shadow: 0px 4px 24px rgba(105, 105, 105, .1);
  --button-green-color: #bbf330;
  --button-green-color--hover: #b5ff00;
  --yellow-main-color: #ffe550;
  --yellow-border-color: #ffe553;
  --dark-gray-placeholder-color: #505050;
  --input_color__invalid: #ff705d;
  --input_color__valid: #7eb101;
  --tran-dur-and-tim-fun: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.button-primary {
  min-width: 50px;
  color: inherit;
  text-align: center;
  cursor: pointer;
  transition: background-color var(--tran-dur-and-tim-fun);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  align-items: center;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
}

.button {
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  align-items: center;
}

.button--burger {
  width: 28px;
  height: 28px;
  fill: var(--dark-green-color);
  background-color: var(--primary-background-color);
  transition: box-shadow var(--tran-dur-and-tim-fun), fill var(--tran-dur-and-tim-fun);
  border-style: none;
  padding: 6px 2px;
}

.button--burger__icon {
  width: 24px;
  height: 16px;
}

.button--form-submit--footer {
  height: 42px;
  background-color: var(--primary-background-color);
  border: 1px solid var(--dark-green-color);
  transition: background-color var(--tran-dur-and-tim-fun);
  border-radius: 30px;
  margin: 20px 0 20px auto;
  padding: 5px 20px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.33;
  display: block;
}

.button--form-submit--footer:focus, .button--form-submit--footer:focus-visible, .button--form-submit--footer:hover {
  background-color: var(--yellow-main-color);
}

@media screen and (min-width: 768px) {
  .button--form-submit--footer {
    margin: 0;
  }
}

.button--form-submit--modal {
  height: 42px;
  background-color: var(--primary-background-color);
  border: 1px solid var(--dark-green-color);
  transition: background-color var(--tran-dur-and-tim-fun);
  border-radius: 30px;
  margin: 15px auto 0;
  padding: 5px 20px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.33;
  display: block;
}

.button--form-submit--modal:focus, .button--form-submit--modal:focus-visible, .button--form-submit--modal:hover {
  background-color: var(--yellow-main-color);
}

.button--close-modal {
  width: 30px;
  height: 30px;
  border-style: none;
  border: 1px solid var(--dark-green-color);
  transition: background-color var(--tran-dur-and-tim-fun);
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.button--close-modal__icon {
  width: 12px;
  height: 12px;
}

.button--close-modal:focus, .button--close-modal:hover {
  background-color: var(--yellow-main-color);
}

.button--order-now--header {
  display: none;
}

@media screen and (min-width: 768px) {
  .button--order-now--header {
    vertical-align: middle;
    white-space: nowrap;
    background-color: var(--button-green-color);
    transition: background-color var(--tran-dur-and-tim-fun);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 30px;
    padding: 7px 17px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    display: inline-block;
  }

  .button--order-now--header:focus, .button--order-now--header:hover {
    background-color: var(--btn-1_clr__hover);
  }
}

.salat-slider {
