.header .mobile-menu {
  display: block;
  position: relative;
}

.header .mobile-menu-nav-list {
  background-color: var(--primary-background-color);
  box-shadow: var(--hdr_shadow);
  transform-origin: 100% 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: box-shadow var(--tran-dur-and-tim-fun), opacity var(--tran-dur-and-tim-fun), transform var(--tran-dur-and-tim-fun), visibility var(--tran-dur-and-tim-fun);
  border-radius: 30px 0 30px 30px;
  display: block;
  position: absolute;
  top: 100%;
  right: 0;
  overflow: hidden;
  transform: scale(0);
}

.header .mobile-menu-nav-list.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: scale(1);
}

.header .mobile-menu-nav-list__link {
  min-width: 130px;
  height: 51px;
  vertical-align: middle;
  color: #000;
  transition: background-color var(--tran-dur-and-tim-fun);
  padding: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  display: inline-block;
}

