.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding: 0 16px;
  }
}

.hero-offerings-wrapper {
  margin: 0 auto;
  max-width: 480px;
  background-color: var(--primary-background-color);

  @media screen and (min-width: 768px) {
    background-image: url(../images/offerings/bgi-md.svg);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    max-width: 768px;
  
  }

